import "react-app-polyfill/ie11";
import "react-app-polyfill/stable";

import { ConfigProvider } from "antd";
import React from "react";
import ReactDOM from "react-dom";
import App from "./App";
import { AppProvider } from "./context";

import "./index.less";
import { AppLoader } from "./components/AppLoader";

// declare global {
//     interface Window {
//         Affinity: {
//             DBWDomReady: any;
//             DBWDomData: any;
//         };
//     }
// }

(function (d) {
    const head = d.head || d.getElementsByTagName("head")[0];
    const APP_CONTAINER = document.getElementById("SS-DashboardWrapper");

    const gFontMontserrat =
        d.getElementById("GFontMontserrat") ||
        d.querySelector("link[href*=Montserrat");
    if (!gFontMontserrat) {
        const hash =
            APP_CONTAINER?.dataset.hash ||
            APP_CONTAINER?.getAttribute("data-hash") ||
            "58cf466dd01c40a05a3b1fde97efa37462d57ba125e730d10a1346a80266e462==";
        const f = d.createElement("link");
        f.setAttribute(
            "href",
            "https://use.typekit.net/cgw2rkw.css"
        );
        f.setAttribute("rel", "stylesheet");
        f.setAttribute("id", "GFontMontserrat");
        f.setAttribute("nonce", hash);
        head.appendChild(f);
    }

    // if (!window.Affinity) {
    //     window.Affinity = { DBWDomReady: null, DBWDomData: {} };
    // }
    // window.Affinity.DBWDomReady = document.createEvent("HTMLEvents");
    // window.Affinity.DBWDomReady.iniEvent("DBWDomReady", true, false);

    // window.Affinity.DBWDomData = {};

    ReactDOM.render(
        <ConfigProvider prefixCls="ss-dashboard-wrap">
            <React.StrictMode>
                <AppProvider>
                    <React.Suspense
                        fallback={
                            <AppLoader isAppWrap={true} tip="Loading..." />
                        }
                    >
                        <App />
                    </React.Suspense>
                </AppProvider>
            </React.StrictMode>
        </ConfigProvider>,
        APP_CONTAINER
    );
})(document);
