import React from "react";
import { Button, Dropdown, Menu } from "antd";
import { AppIcon } from "./SVGFiles";
import { AppContext } from "../context";

const APP_CONTAINER = document.getElementById("SS-DashboardWrapper");

interface Props {
    onMenuClick: () => void;
}

export const UserTopNav: React.FC<Props> = ({ onMenuClick }) => {
    const { state } = React.useContext(AppContext);
    const { user, formLinks } = state;

    const userMenu = (
        <Menu className="ss-dashboard-wrap-dd-profile">
            {formLinks && (
                <Menu.Item key="0">
                    {formLinks.map((navProps, i) => {
                        return (
                            <a role={"button"} href={navProps.link}>
                                <AppIcon
                                    asset={navProps.icon}
                                    stroke="#000000"
                                    height="16"
                                    width="16"
                                />
                                <span>{navProps.label}</span>
                            </a>
                        );
                    })}
                </Menu.Item>
            )}
            <Menu.Item key="1">
                <a
                    role={"button"}
                    href={process.env.REACT_APP_LOGOUT_URL as string}
                >
                    <AppIcon
                        asset={"logout"}
                        stroke="#000000"
                        height="16"
                        width="16"
                    />
                    <span>Logout</span>
                </a>
            </Menu.Item>
        </Menu>
    );

    const employeeName = React.useMemo(() => {
        if (user) {
            const { FirstName, LastName } = user;
            return <span>{[FirstName, LastName].join(" ")}</span>;
        }
        return "";
    }, [user]);

    // const employeeAcro = React.useMemo(() => {
    //     if (user) {
    //         const { lastName, firstName } = user;
    //         return (
    //             <Avatar size={44} className="f-12 f-bold m-l-4 m-r-4">
    //                 {[firstName[0], lastName[0]].join("")}
    //             </Avatar>
    //         );
    //     }
    //     return "";
    // }, [user]);

    return (
        <div className="ss-dashboard-wrap-user-top-nav">
            <span className="ss-dashboard-wrap-user-name">
                <span className="ss-dashboard-wrap-mobile-name">
                    {employeeName}
                </span>
                <Dropdown
                    overlay={userMenu}
                    trigger={["click"]}
                    arrow
                    placement="bottomRight"
                    overlayClassName="ss-dashboard-wrap-profile-overlay"
                    getPopupContainer={() => APP_CONTAINER || document.body}
                >
                    <span className="ss-dashboard-wrap-user-dd">
                        {/* {employeeAcro} */}
                        <span className="m-r-8">{employeeName}</span>
                        <AppIcon asset="arrow-down" height="16" width="16" />
                    </span>
                </Dropdown>
            </span>

            {/* <Button
                className="m-l-4"
                type="link"
                shape="circle"
                icon={
                    <AppIcon
                        asset="setting"
                        stroke="#272727"
                        height="30"
                        width="30"
                    />
                }
            /> */}
            <Button
                className="m-l-4"
                type="link"
                shape="circle"
                icon={<AppIcon asset="help" height="30" width="30" />}
                onClick={() =>
                    window.open(
                        process.env.REACT_APP_HELP_URL as string,
                        "_blank"
                    )
                }
            />
            <Button
                className="m-l-4 menu-burger"
                type="link"
                shape="circle"
                icon={<AppIcon asset="burger-menu" />}
                onClick={() => onMenuClick()}
            />
        </div>
    );
};
